import {Card, Col, Button, Typography, Image} from "antd"
const {Text} = Typography
const {Meta} = Card
export const CardItem = props => {
  const {
    id,
    name,
    description,
    full_background,
    price,
    addToCart = Function.prototype
  } = props

  const styles = {
    width: "340px",
    height: "100%",
    display: "flex",
    flexDirection: "column",
    justifyContent: "space-between"
  }

  return (
    <Col className="gutter-row" span={6}>
      <Card
        hoverable
        style={styles}
        cover={
          <Image
            width={"100%"}
            src={"error" && full_background}
            fallback="data:image/png;base64,"
          />
        }
        actions={[
          <Button type="primary" onClick={() => addToCart({id, name, price})}>
            Купить
          </Button>,
          <Text type="success">{price} руб</Text>
        ]}
      >
        <Meta title={name} description={description} />
      </Card>
    </Col>
  )
}
